import TableCustom from '@/components/table/Table'
import { dateTime } from '@/helpers/timeformat'
import InlineItem from '@/components/helper/InlineItem'

export default {
  components: {
    TableCustom,
    InlineItem
  },
  data: () => ({
    fetchData: false,
    popup: null,
    requestTemp: {
      show: 10,
      page: 1
    }
  }),
  methods: {
    dateTime(value){
      if(!value) return '-'
      return dateTime(value)
    },
    curNumber(index){
      return index + (this.requestTemp.show * (this.requestTemp.page - 1)) + 1
    },
    setTime() {
      this.fetchData = true
      clearTimeout(this.typingTimer);
      this.typingTimer = setTimeout(this.searchDataTimer, 1000);
    },
    clearTime() {
      clearTimeout(this.typingTimer);
    },
    searchDataTimer(){
      this.getDataPage(1)
    },
    togglePopup(value){
      this.popup = this.popup == value ? null : value
    },
    setSortBy(value){
      if(value == this.request.sortBy){
          if(this.request.sorting == 'DESC'){
              this.request.sorting = 'ASC'
          }else{
              this.request.sorting = 'DESC'
          }
      }else{
          this.request.sorting = 'DESC'
      }
      this.request.sortBy = value
      this.request.page = 1
      this.getData()
    },
    setShow(value){
      this.request.show = value
      this.getData()
    },
    getDataPage(page){
      this.request.page = page
      this.getData()
    },
  }
}