<template>
    <div class="r-table-pagination">
        <div class="r-pagination r-center-flex r-isWrap">
        <div class="r-mr-auto r-center-flex r-mt-10">
            <span>Data per halaman: </span> 
            <div class="r-pagination-show" @mouseleave="pageDropdown = false">
                <div class="r-pagination-value" @click.prevent="pageDropdown = !pageDropdown">{{request.show}}</div>
                <div class="r-pagination-items" v-if="pageDropdown">
                    <div class="r-pagination-item" v-for="(item, index) in pageShow" :key="`paginationshow-${index}`" @click.prevent="setShow(item)" >{{item}}</div>
                </div>
            </div>
        </div>
        <div class="r-ml-auto r-center-flex r-mt-10">
            <span>{{currentPage()}} dari {{meta.total || 0}}</span>
            <div class="r-pagination-movebutton r-center-flex">
                <input type="image" style="transform: rotate(180deg)" :src="require('@/assets/icons/primary/arrow-right.svg')" :disabled="request.page < 2" @click.prevent="getData(-1)">
                <input type="image" :src="require('@/assets/icons/primary/arrow-right.svg')" :disabled="request.page >= meta.last_page" @click.prevent="getData(1)">
            </div>
        </div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        request: {
            require: true
        },
        meta: {
            require: false
        }
    },
    data: () => ({
        pageDropdown: false,
        pageShow: [10,25,50,100]
    }),
    methods: {
        setShow(value){
            this.pageDropdown = false
            this.$emit("setShow", value)
        },
        getData(value){
            const current = this.meta.current_page
            const nextPage = current + value
            if(nextPage > 0 && nextPage <= this.meta.last_page) this.$emit("getData", nextPage)
        },
        currentPage(){
            const current = this.meta.current_page
            const firstValue = (current - 1) * this.meta.per_page + 1
            const lastValue = firstValue + this.meta.per_page - 1
            const currentLast = lastValue > this.meta.total ? this.meta.total : lastValue
            return `${firstValue} - ${currentLast}`
        }
    }
}
</script>