<template>
<InputModal 
  v-if="modal == 'inputUser'"
  :type="type"
  @closeModal="modal = null" 
  @getData="getData"
  >
  </InputModal>
<DeleteModal 
  v-if="modal == 'userDelete'" 
  @closeModal="modal = null" 
  @getData="getData"
  >
  </DeleteModal>
<ResetPasswordModal 
  v-if="modal == 'resetPassword'" 
  @closeModal="modal = null" 
  @getData="getData"
  >
  </ResetPasswordModal>

<div class="r-box r-plr-16 r-ptb-8 r-white-bg">
  <div class="r-center-flex">
    <div class="r-wd-230-max r-mr-auto">
      <InputCustom
        v-model="request.search"
        label="Cari"
        :withError="false"
        placeholder="Cari . ."
        :rightIcon="true"
        @keyup="setTime()" 
        @keydown="clearTime()" 
        @keydown.tab="setTime()" 
        @keyup.tab="clearTime()" 
        @keypress.enter="getData()"
      >
        <template #rightIcon>
          <span class="material-icons">search</span>
        </template>
      </InputCustom>
    </div>
    <ButtonCustom @click.prevent="openInputModal()">
      Tambah Data
    </ButtonCustom>
  </div>
</div>

<div class="r-box r-plr-16 r-ptb-8 r-white-bg r-mt-20">
    <TableCustom 
        :columns="columns" 
        :request="request" 
        :fetchData="fetchData"
        :data="data"
        :meta="meta"
        :isEmpty="data.length == 0"
        @setSortBy="setSortBy"
        @setShow="setShow" 
        @getData="getDataPage"
        @mouseleave="popup = null"
        @togglePopup="togglePopup"
        >
        <template #index="props">
          <div class="r-center-flex r-isRelative">
            {{curNumber(props.index)}}
            <span class="material-icons r-isNotif" v-if="props.row.updated_at != props.row.publish_at">circle</span>
          </div>
        </template>
        <template #updated_at="props">
          {{dateTime(props.row.updated_at)}}
        </template>
        <template #publish_at="props">
          {{dateTime(props.row.publish_at)}}
        </template>

        <template #action="props">
          <div class="r-menu">
              <!-- <img :src="require('@/assets/icons/others/more.svg')"> -->
              <div class="r-menu-items" v-if="popup == curNumber(props.index)" >
                    <div class="r-menu-item" @click.prevent="openInputModal('Ubah',props.row)">Ubah</div>
                    <div class="r-menu-item" >Publish </div>
                    <div class="r-menu-item"  @click.prevent="openConfirmModal(props.row,'resetPassword')">Reset Password </div>
                    <div class="r-menu-item-red" @click.prevent="openConfirmModal(props.row,'userDelete')">Hapus</div>
              </div>
          </div>
        </template>
    </TableCustom>
</div>
</template>
<script>
import metatag from '@/mixins/metatag'
import breadcrumb from '@/mixins/breadcrumb'
import common from '@/mixins/common'
import InputModal from '@/components/user/InputModal'
import DeleteModal from '@/components/user/DeleteModal'
import ResetPasswordModal from '@/components/user/ResetPasswordModal'
import tableMixins from '@/mixins/table'
import { mapActions, mapGetters } from 'vuex'

export default {
  mixins: [common, metatag, breadcrumb, tableMixins],
  computed: {
    ...mapGetters({
      data: 'user/getData',
      meta: 'user/getMeta',
      request: 'user/getRequest',
    }),
  },
  components: {
    InputModal,
    DeleteModal,
    ResetPasswordModal
  },
  data: () => ({
    search: null,
    modal: null,
    type: 'Tambah',
    columns: [
        {
            label: 'No',
            isSortable: false,
            fieldName: 'index',
            class: "r-table-firstColumn"
        },
        {
            label: 'Terakhir Diubah',
            isSortable: false,
            fieldName: 'updated_at',
            class: ""
        },
        {
            label: 'Terakhir Dipublish',
            isSortable: false,
            fieldName: 'publish_at',
            class: ""
        },
        {
            label: 'Nama',
            isSortable: false,
            fieldName: 'name',
            class: ""
        },
        {
            label: 'Username',
            isSortable: false,
            fieldName: 'username',
            class: ""
        },
        {
            label: 'Action',
            isSortable: false,
            fieldName: 'action',
            class: "r-table-actionColumn"
        }
    ]
  }),
  mounted(){
    this.setMetaTag() //default
    this.showBreadcrumb()
    this.initData()
  },
  methods: {
    ...mapActions({
      getItemData: 'user/fetchData',
      resetItem: 'user/resetItem',
      setItem: 'user/setItem',
      setRequest: 'user/setRequest'
    }),
    showBreadcrumb(){
      const breadcrumb = [
          {
              label: 'Pengguna',
              route: {name: null},
              translateAble: false
          }
      ]
      this.setBreadcrumb(breadcrumb)
    },
    prepareEditData(item){
      const payload = {
        id: item.id,
        name: item.name,
        username: item.username
      }
      return payload
    },
    openInputModal(type = 'Tambah', item = {}){
      if(type == 'Tambah') {
        this.resetItem()
      }
      else {
        const payload = this.prepareEditData(item)
        this.setItem(payload)
      }
      this.type = type
      this.modal = 'inputUser'
    },
    openConfirmModal(item, modal){
      this.modal = modal
      const payload = this.prepareEditData(item)
      this.setItem(payload)
    },
    async initData() {
      await this.getData()
    },
    async getData() {
      this.fetchData = true
      await this.getItemData()
      this.requestTemp = {...this.request}
      this.fetchData = false
    }
  }
}
</script>

