<template>
    <Modal :label="`${type} Pengguna`" @closeModalPopup="closeModal">

        <InputCustom
            v-model="item.name"
            label="Nama"
            :error="error.name"
            :errorMsg="errorMsg.name"
            @onFocus="resetError('name')"
        >
        </InputCustom>

        <InputCustom
            v-model="item.username"
            label="Username"
            :error="error.username"
            :errorMsg="errorMsg.username"
            @onFocus="resetError('username')"
        >
        </InputCustom>

        <div class="r-center-flex r-isExpand">
              <ButtonCustom :disabled="isLoading" class="r-ml-auto r-mr-10" color="base-20" @click.prevent="closeModal()">
                Batal
              </ButtonCustom>
              <ButtonCustom :isLoading="isLoading" @click.prevent="sendData">
                {{type}}
              </ButtonCustom>
        </div>
    </Modal>
</template>
<script>
import Modal from '@/components/helper/Modal'
import common from '@/mixins/common'
import validation from '@/mixins/validation'
import { mapGetters, mapActions } from 'vuex'

export default {
  emits: ['closeModal','getData'],
  mixins: [common, validation],
  props: {
    type: {
      require: true
    }
  },
  components: {
    Modal,
  },
  computed: {
    ...mapGetters({
      item: 'user/getItem'
    })
  },
  data: () => ({
    show: null,
    isLoading: false,
    error:{
      name: false,
      username: false,
    },
    errorMsg:{
      name: 'Tidak Boleh Kosong',
      username: 'Tidak Boleh Kosong',
    }
  }),
  methods: {
    ...mapActions({
      store: 'user/store',
      update: 'user/update',
    }),
    closeModal(){
      this.$emit('closeModal')
    },
    async validation(){
        const field = ['name','username']
        return await this.validateForm(field,this.item)
    },
    async sendData() {
        const errorcount = await this.validation()
        if(errorcount == 0){
            this.isLoading= true
            if(this.type == 'Tambah') await this.store(this.item)
            else await this.update(this.item)
            this.isLoading= false
            const response = this.getResponse('user')
            this.showSnackbar({
              type: response.status == 1 ? 'success': 'error',
              text: response.message
            })
            if(response.status == 1){
              this.$emit('getData')
              this.closeModal()
            }
        }else{
            this.showSnackbar({
              type: 'error',
              text: 'Mohon Periksa Kembali'
            })
        }
    },
  }
}
</script>