<template>
  <teleport to="#modal">
    <div class="r-modal" @click.stop="bgClick">
        <div class="r-modal-body" :style="popup ? 'overflow: visible;' : ''" @click.stop="">
            <div class="r-modal-header">
               
                <slot name="box-header">
                    <div class="r-center-flex">
                        <h4 class="r-mr-auto">
                            {{label}}
                        </h4>
                        <!-- <img class="r-isClickAble" width="20" height="20" :src="require('@/assets/icons/black/close.svg')" alt="close" @click.prevent="closeModalPopup()" > -->
                    </div>
                </slot>
            </div>
            <div class="r-modal-value">
                <slot></slot>
            </div>
        </div>
    </div>
  </teleport>
</template>
<script>
export default {
    emits: ['bgClick','closeModalPopup'],
    props: {
        label: {
            default: null
        },
        popup: {
            default: false
        }
    },
    methods: {
        bgClick(){
            this.$emit('bgClick')
        },
        closeModalPopup(){
            this.$emit('closeModalPopup')
        }
    }
}
</script>