<template>
    <Modal label="Hapus Data" @closeModalPopup="closeModal">

        <div class="r-delete-info">
            <div class="r-mr-10 r-pt-4">
                <!-- <img :src="require(`@/assets/icons/toast/warning.svg`)" alt=""> -->
            </div>
            <div class="r-deleted-data">
                <p>Apakah anda yakin ingin menghapus data ini ?</p>
                <table class="r-delete-table">
                    <tr>
                        <td>Nama</td>
                        <td>:</td>
                        <td>{{item.name}}</td>
                    </tr>
                    <tr>
                        <td>Username</td>
                        <td>:</td>
                        <td>{{item.username}}</td>
                    </tr>
                </table>
            </div>
        </div>

        <div class="r-center-flex r-isExpand">
              <ButtonCustom class="r-ml-auto r-mr-10" color="base-20" @click.prevent="closeModal()">
                Batal
              </ButtonCustom>
              <ButtonCustom color="error" :isLoading="isLoading" @click.prevent="sendData">
                Hapus
              </ButtonCustom>
        </div>
    </Modal>
</template>
<script>
import Modal from '@/components/helper/Modal'
import common from '@/mixins/common'
import { mapGetters, mapActions } from 'vuex'

export default {
  emits: ['closeModal','getData'],
  mixins: [common],
  props: {
    modal: {
      default: null
    }
  },
  components: {
    Modal,
  },
  computed: {
    ...mapGetters({
      item: 'user/getItem'
    })
  },
  data: () => ({
    isLoading: false,
  }),
  methods: {
    ...mapActions({
      destroy: 'user/destroy',
    }),
    closeModal(){
      this.$emit('closeModal')
    },
    async sendData() {
        this.isLoading= true
        await this.destroy(this.item)
        this.isLoading= false
        const response = this.getResponse('user')
        this.showSnackbar({
            type: response.status == 1 ? 'success': 'error',
            text: response.message
        })
        if(response.status == 1){
            this.$emit('getData')
            this.closeModal()
        }
    },
  }
}
</script>